body {
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f4f4f4;
}

.App {
  text-align: center;
  padding: 20px;
  max-width: 900px;
  margin: 0 auto;
  background-color: white;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0,0,0,0.1);
}

.header {
  background-color: #282c34;
  color: white;
  padding: 20px;
}

.header h1 {
  margin: 0;
}

.about-me, .skills, .projects, .contact {
  margin-top: 40px;
  padding: 20px;
}

.skills p {
  margin-top: 10px;
}

.project-list {
  display: flex;
  justify-content: space-around;
}

.project {
  background-color: #f7f7f7;
  padding: 20px;
  width: 45%;
  margin: 10px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0,0,0,0.1);
}

.social-icons a {
  margin: 0 10px;
}

footer {
  margin-top: 40px;
  padding: 20px;
  background-color: #282c34;
  color: white;
}
